	        "description": "<p>Questions concerning Technology and Biology</p> <p>In which arenas are biology &amp; technology cooperating, complementing each other, coevolving and possibly merging, and in which are they competing, merely mimicking one another, or pulling towards different ends? What sort of boundary work between the made and the born is involved in these processes? Each panel engages a different type of relationship between biology and technology that is addressed by the multiple perspectives of science,humanities, and the arts.</p> <p>\n\n\n<img src=\"https://a248.e.akamai.net/secure.meetupstatic.com/photos/event/a/4/1/600_456062625.jpeg\" /></p> <p><b>December 19 (Monday)</b></p> <p><br/><b>9:30-10:00 Opening</b><br/>Leo Corry, Dean of Humanities, Tel Aviv University<br/>Jose Brunner, Director, Cohn institute, Tel Aviv University</p> <p><br/><b>10:00-12:00 Origins</b><br/>Moshe Kol, Tel Aviv University<br/>Evolution of Alternative Life Forms from an Inorganic/Organic Chemist\u2019s Perspective</p> <p>Raz Chen-Morris, The Hebrew University of Jerusalem<br/>The Eye as an Organ and Instrument in Early Modern Europe</p> <p><br/>Elana Gomel, Tel Aviv University<br/>Biological Sublime: Biology, Technology, Science Fiction</p> <p><br/>Chair/commentator: Eran Hadas, poet, software developer, and new media artist</p> <p><b>12:00-13:00 Lunch</b></p> <p><br/><b>13:00-15:00 The Anthropocene: Mimicry and Beyond</b></p> <p><br/>Dana Zelig, Bezalel Academy of Arts and Design<br/>Growing Patterns</p> <p><br/>Yoram Reich, Tel Aviv University<br/>Designing a Dialogue between the Natural and the Artificial:What is the Purpose?</p> <p>Pieter Lemmens, Radboud University Nijmegen, The Netherlands Reflections on Digital Technology and Intelligence in the Anthropocene</p> <p><br/>Chair/commentator: Daniel Mishori, Tel Aviv University</p> <p><br/><b>15:00-15:30 Coffee break</b></p> <p><br/><b>15:30-17:30 Biology as Technology</b><br/>Ehud Shapiro, Weizmann Institute of Science<br/>Programming Languages for Life</p> <p>Christopher Coenen, Karlsruhe Institute of Technology, Germany Programmable Matter</p> <p><br/>Denisa Kera, Charles University, Prague, Czech Republic<br/>Tinkering with the Blockchain: Ontogeny and Phylogeny of Networks</p> <p><br/>Chair/commentator: Doron Friedman, IDC Herzliya</p> <p><br/><b>17:30-18:00 Coffee break</b></p> <p><br/><b>18:00-19:30 Keynote</b><br/>Peter-Paul Verbeek, Twente University, The Netherlands<br/>Sapiens ex Machina: Towards an Anthropology of Human-Robot Relations</p> <p>\n\n\n\n<b>December 20 (Tuesday)</b></p> <p><br/><b>9:00-11:00 The Technologization of Biology</b></p> <p><br/>Noam Shomron, Tel Aviv University<br/>The Human Genome Revolution</p> <p><br/>Tamar Sharon, Maastricht University, The Netherlands The\xA0 Googlization of Medicine</p> <p><br/>Nurit Bar Shai, artist<br/>Artists and Citizen Scientists at the Forefront of Biotechnology</p> <p><br/>Chair/commentator: Snait Gissis, Tel Aviv University</p> <p><br/><b>11:00-11:30 Coffee break</b></p> <p><br/><b>11:30-13:30 Cyborg 1: Between Disability and Enhancement</b></p> <p><br/>Iddo Gruengard, multidisciplinary artist<br/>The Void between Technology and the Body</p> <p><br/>Amir Amedi, The Hebrew University of Jerusalem<br/>Challenges and Opportunities in using Brain Implants and what it teaches us about the Brain</p> <p>Dov Greenbaum, Yale University &amp; IDC Herzliya<br/>When Disabilities are Desired Abilities</p> <p><br/>Chair/commentator: Carmel Vaisman, Tel Aviv University<b><br/></b></p> <p><b>13:30-14:30 Lunch break</b></p> <p><b>14:30-16:30 Cyborg 2: Synergy in Virtual/Augmented Reality</b></p> <p>Galit Wellner, The NB School of Design &amp; Tel Aviv University<br/>Postphenomenology of AR</p> <p><br/>Daniel Landau, Aalto University, Finland<br/>Narrative and Re-embodiment in Virtual Reality</p> <p><br/>Miriam Reiner, Technion-Israeli Institute of Technology<br/>The Synergetic Process of Virtual/Augmented Reality and Enhanced Human Cognition: Two Examples</p> <p>Chair/commentator: Nea Erlich, Van Leer Institute</p> <p><br/><b>16:30-17:00 Coffee break<br/>17:00-17:15 Continuing the dialogue: upcoming events<br/></b></p> <p>Carmel Vaisman, Tel Aviv University</p> <p><br/>Lior Zalmanson, New York University &amp; Print Screen Festival</p> <p><br/><b>17:15-19:15 Back to the Future</b><br/>Ehud Gazit, Tel Aviv University<br/>Molecular Self-Organization: From the Origin of Life to 21st Century Technology</p> <p>Oron Catts, The University of Western Australia<br/>Growing the Made: The Industrialization of Biology vs. the Biologization of Industry - A Cultural Perspective</p> <p>Ehud Lamm, Tel Aviv University<br/>Why Biologists Didn't like Math? Or, is it Time for a New Macy Conference?</p> <p><br/>Chair/commentator: Keren Omry, University of Haifa</p> ",
