	        "link": "https://www.meetup.com/ILTechTalks/events/236022476/",
	        "description": "<p><img src=\"https://a248.e.akamai.net/secure.meetupstatic.com/photos/event/4/b/f/4/600_446059444.jpeg\" /></p> <p>In this meetup Arik Lerner, Automation Team Leader, and Waseem Hamshawi, Automation Infra Developer, will present how to build a large scale automated testing platform by leveraging containers orchestration over GCP, with the ability to scale out and provide fast feedback while maintaining a highly reliable test infrastructure.\xA0</p> <p><b>What will you learn?\xA0</b></p> <p>New approach of managing a scalable testing platform of distributed automated tests with Kubernetes and Docker over Google Cloud Platform.\xA0</p> <p><b>Agenda:\xA0</b></p> <p>\u2022\xA0GCP and Kubernetes introduction for automated testing\xA0</p> <p>\u2022\xA0Traditional Selenium Grid vs Selenium Standalone with \xA0 \xA0 \xA0 \xA0Kubernetes and Docker for Web and Mobile tests\xA0</p> <p>\u2022\xA0Distributed and containerized testing environment over container cluster - different use cases\xA0</p> <p><a href=\"https://github.com/LivePersonInc/ephemerals\">Ephemerals</a> - \"Short-lived Testing Endpoints\". An Open Source by LivePerson which makes automation testing at large scale like a \"Walk in the park\".\xA0<br/>You can download the open source [<a href=\"https://github.com/LivePersonInc/ephemerals\">here</a>]</p> <p>\u2022\xA0Demo\xA0</p> <p>\u2022\xA0Q&amp;A</p> <p><b>Schedule:\xA0</b></p> <p>17:00-17:30 - Gathering with Food &amp; Beers<br/>17:30 - We're starting</p> <p><b> House Keeping Information:\xA0</b></p> <p>- FREE Parking is available in the building and in the outdoor parking lot in front of the gas station</p> <p>- Lecture Language: English\xA0</p> <p>- This Meetup will be video recorded\xA0</p> <p><i>Join us</i>!</p> <p><img src=\"https://a248.e.akamai.net/secure.meetupstatic.com/photos/event/4/b/f/5/600_446059445.jpeg\" /></p> ",
