	        "link": "https://www.meetup.com/Reactive-Extensions-Rx-Israel/events/235594559/",
	        "description": "<p>Rx-Israel 2nd meetup!<br/>Quite some time has passed from our last meetup, like before, we are going to have a few talks on Rx and other \"reactive\" related topics.<br/>Our talks are a bit more advanced then our last meetup, so it's recommended to have some basic familiarity/experience with Rx. </p> <p><b>Agenda </b></p> <p>17:50 - <b>Gathering </b></p> <p>18:10 - <b>Re-implementing observable from scratch<br/>(by Aviv Rozenboim) </b></p> <p>Dive into Observables, and discover how a few simple, easily understandable functions are composed to create an Observable, and gain a better understanding of what happens behind the scenes in an Observable.</p> <p>18:40 - <b>Reactive React<br/>(by Zahi cohen) </b></p> <p>In this talk we will build a multiplayer app using React, Recompose &amp; Firebase, all connected together by Rx Observables.</p> <p>19:15 - <b>Break </b></p> <p>19:30 -  The mysteries of Observables of Observables...<br/><b>(by Yshay Yaacobi) </b></p> <p>Flattening an Observable is quite common in rx programming, yet choosing the right operator might be challenging...<br/>In this talk, we'll walk through some of these flattening operators that are identical in signature but totally different in behavior.</p> ",
