	        "link": "https://www.meetup.com/StartHolon/events/236028431/",
	        "description": "<p>Whether if you're bootstrapping, funded by FFFs, angel or a VC.</p> <p><br/>You have funds, and sooner or later you're going to burn them.</p> <p>In order to raise more money and keep your startup above water, you will need to handle your funds wisely, and burn it efficiently.</p> <p>\n\n\nIn our next meetup, we're happy to host Stephan Guberman ,CEO Guberman group.</p> <p>Guberman Group is a well known accounting and finance office in the Israeli startup eco-system, with many years of experience and successful companies.</p> <p>Now it's time to share their experience with StartHolon.</p> <p>\n\n\n<b>Agenda:</b></p> <p>* 18:30 Networking</p> <p>* 19:00 Handle your funds wisely</p> <p>* 20:00 Q&amp;A</p> <p>\n\n\n----------------------------------------------------------------------------</p> <p>בדרך לגיוס מחברים ומשפחה ? בסבב פגישות עם אנג'לים ? עובדים על המצגת לקרנות הון- הסיכון ?</p> <p>בכל אחד מהמקרים הבאים, אתם כנראה עומדים לגלות תחום חדש, שיכול לחרוץ את גורל המיזם שלכם עוד בטרם גייסתם את השקל הראשון.</p> <p>תחום הפיננסים!</p> <p>ולא, אנחנו לא מדברים על הסכום שאותו אתם רוצים לגייס או על הסכום שתקבלו באקזיט , אנו מדברים על ההתנהלות הפיננסים שלכם בדרך לשם..</p> <p>המשקיעים הרציניים יראו בהתנהלות שלכם בטרם לגיוס כמדד להצלחת המיזם בהמשך.</p> <p>אז בכדי לרכז עבורכם את הנושא ולתת לכם כלים ממשיים להתמודדות עם העתיד לבוא,</p> <p>אנו נארח במפגש הבא שלנו את סטפן גוברמן, מנכ\"ל מקבוצת גוברמן אשר מלווה חברות כמו גוגל, פייפאל, איירונסורס ומאות סטארטאפים נוספים.</p> <p>במסגרת הרצאתו בנושא \"התנהלות פיננסית של סטארטאפ בתחילת דרכו\" סטפן ישוחח על הייחוד שבסטארט-אפ וצרכיו החשבונאיים, תיאור מערך הכספים והחלטות קריטיות ראשוניות כמו מתי להתמסד ולהקים חברה </p> <p><b>אג׳נדה:</b></p> <p>18:30 - נטוורקינג</p> <p>19:00 - 20:00 הרצאה</p> <p>\n\n\n<img src=\"https://a248.e.akamai.net/secure.meetupstatic.com/photos/event/a/e/d/3/600_456584755.jpeg\" /></p> ",
