	        "link": "https://www.meetup.com/full-stack-developer-il/events/235547825/",
	        "description": "<p><b>Are you a Java developer who wants to program in\xA0Scala?</b></p> <p>In this course you will learn to program in the\xA0Scala\xA0language by\xA0Evyatar Kafkafi\xA0from <a href=\"http://www.tikalk.com/hebrew/\">Tikal</a>.</p> <p><b>You will be one of 10 participants</b> (or maybe even less),<br/><b>the course has 5 sessions (each of them 3 hours long), and\xA0You MUST participate in all 5 sessions. There is no homework!</b></p> <p><b>** To register you must <a href=\"https://docs.google.com/forms/d/e/1FAIpQLSeocDbZZnqQUKH7Wo_gJTNx_ZP2VxrkUT7Qh9WtWiHprdBD0A/viewform\">fill out this form</a>, so we can get back to you to arrange your participation**\xA0<br/></b></p> <p>In the first 3 sessions we will study together how to program in\xA0Scala.\xA0<br/>Then we will have 2 sessions in which we will work on a project that all of us develop together.</p> <p><b>*This\xA0Scala course is targeted for experienced Java developers (2 years or more of experience of developing with the Java language).</b></p> <p><br/>You do NOT need to have any prior knowledge of\xA0Scala.</p> <p>In the end of the course you will reach a level of:</p> <p>1. programming simple tasks independently, in\xA0Scala</p> <p>2. being able to continue learning\xA0Scala\xA0on your own, from books, videos, and internet resources.</p> <p>3. understand\xA0Scala\xA0code samples in lectures and presentations on other topics (such as Spark, Akka, etc)</p> <p>\n\n\n<b>Syllabus\xA0of the course:</b></p> <p>\u2022\xA0Writing\xA0Scala\xA0code in a regular Maven project, using the IDE</p> <p>\u2022\xA0Scala\xA0classes, methods, object instantiation, constructors, inheritance</p> <p>\u2022\xA0Conditionals and loops</p> <p>\u2022\xA0Types, Operators, syntax of method calls and passing arguments to methods</p> <p>\u2022\xA0Traits</p> <p>\u2022\xA0Companion objects, the apply() method</p> <p>\u2022\xA0Pattern matching</p> <p>\u2022\xA0Case classes</p> <p>\u2022\xA0Tuples</p> <p>\u2022\xA0Collections (Lists, Maps)</p> <p>\u2022\xA0\xA0High Order functions</p> <p>\u2022\xA0\xA0Filter and map</p> <p>\u2022\xA0Functional Programming</p> <p>\u2022\xA0Other\xA0Scala\xA0language constructs and idioms</p> ",
