	        "link": "https://www.meetup.com/Cutting-edge-tech-and-Big-Data-case-studies/events/236063437/",
	        "description": "<p><b>Optimal+ and Twingo are happy to invite you to a BIG DATA meetup</b></p> <p><br/>This meetup will deal with BIG DATA DB technologies, each speaker will present a premium BIG DATA solution that works for his company, and you will get better understanding how things are working when you provide a large scale DB solutions together with some valuable tips based on real case studies.</p> <p>This is a very ingesting use case, both companies introduce new technologies (Optimal+ choose Vertica together with Hadoop as their solution, appsflyer choose MEMSql with Spark), and managing a BIG DATA challenge -\xA0 come and learn BIG DATA from the best!</p> <p>Agenda:</p> <p>17:00 \u2013 17:30\xA0\u2013 Gathering drinks &amp; food</p> <p>17:30-18:20 -\xA0<b>Alon\xA0Malki\xA0- </b>Chief Architect - Optimal+</p> <p>\"Maximize Hadoop and Vertica big data solution - Ad hoc &amp; Batch analytics on a cost effective Big Data platform\u201D</p> <p>This is the story of how O+ built a cost effective solution on top of Hadoop and Vertica to answer both batch and Ad Hoc requirements while minimizing data duplication and required nodes, bringing a cost effective solution that can be relevant both for big and small customers. O+ big data platform is an OEM solution in PB scale for customers like Qualcomm, Broadcom and nvidia.</p> <p>Five minute break</p> <p>18:25-18:55\xA0-\xA0<b>Morri\xA0</b><b>Feldman</b><b>- </b><b>Data engineer\xA0- AppsFlyer</b></p> <p>Join me on our journey and I will show you the current solution that implements real-time aggregation over MemSQL integrated with the batch processing over Apache Spark. The new architecture solved not only our pains but allowed us to aggregate X10 amount of data with much faster response times, keep up with product demands and it was a cheaper solution from the production cost perspective.</p> <p>We kindly ask -RSVP only if you actually attends to come- there is a 70 person limit</p> ",
