	        "link": "https://www.meetup.com/InsurTech-Takeover/events/235954109/",
	        "description": "<p><i>As part of the first Insurtech Israel Startup Competition, SOSA and\xA0JVP\xA0together with AXA Strategic Ventures, Kamet, Deloitte, Earnix, HFN and Startup Nation Central are proud to present\xA0\"Investment &amp; Innovation in Insurtech.\"\xA0</i></p> <p><i>Meet major players in the global insurance industry and gain the practical insights on what challenges they face and the innovations they seek.</i></p> <p><b>When: December 20th, 18:00</b></p> <p><b>Where: SOSA, Shocken 13, Tel Aviv\xA0</b></p> <p><b>RSVP:</b>\xA0<a href=\"https://www.eventbrite.com/e/investment-innovation-in-insurtech-tickets-29783414996\"><a href=\"https://www.eventbrite.com/e/investment-innovation-in-insurtech-tickets-29783414996\" class=\"linkified\">https://www.eventbrite.com/e/investment-innovation-in-insurtech-tickets-29783414996</a></a></p> <p>\n\n\n<b>Agenda\xA0</b></p> <p>-\xA018:00-18:30 - Gathering.</p> <p>-\xA018:30-18:40 \u2013 Yonatan Machado, Partner, JVP \u2013 Introduction to the InsurTech Competition.</p> <p>-\xA018:40-19:05\xA0 - Joe Guastella, Head of Deloitte\u2019s Financial Services consulting practice.</p> <p>-\xA019:05-19:30 \u2013 Shay Simkin \u2013 CEO of Howden Israel</p> <p><b>-\xA019:30-19:55 \u2013</b><b>Alex Zukerman, VP Product Marketing, Sapiens</b></p> <p>-\xA019:55-20:00 - Closing</p> <p><b>Joe Guastella</b></p> <p>Joe leads Deloitte\u2019s US Financial Services consulting practice and is a member of the Global Financial Services executive team. He is responsible for service delivery in areas of strategy, operations, human capital, technology and risk services. Joe has more than 25 years' experience guiding financial services execs in the design of strategic change management and large scale transformations. Prior to his global and US leadership roles, he led Deloitte\u2019s Asia Pacific Insurance Consulting practice</p> <p>J<b>Shay Simkin</b></p> <p>Shay Simkin is the Managing Director of Howden Insurance Brokers Israel and Head of Cyber for Howden Global (world's largest employee-owned insurance broker). Shay has more than 25 years of experience in the Insurance Broking industry, serving in senior management roles both in Israel and Europe. Shay is a global expert and pioneer in the Cyber Insurance space. As the founder of InsurTec.com back in 1997, he was one of the first Insurance brokers to introduce Cyber &amp; Technology insurance solutions, by drafting cyberspace and technology insurance policies. As a result, Shay has become a world expert in Cyber Liability insurance and is currently leading Howden group's Cyber Liability initiatives worldwide.\xA0</p> <p><br/>More about the Competition:\xA0</p> <p><img src=\"https://a248.e.akamai.net/secure.meetupstatic.com/photos/event/2/f/1/b/600_456492059.jpeg\" /></p> <p>\n\n\n\n\n\n<i><br/></i></p> <p><i><br/></i></p> ",
