	        "link": "https://www.meetup.com/8200-EISP/events/235954471/",
	        "description": "<p><img src=\"http://photos3.meetupstatic.com/photos/event/e/8/4/e/600_456719470.jpeg\" /></p> <p>** PLEASE NOTE: The talk will be held in Hebrew **</p> <p><br/>If you are looking for co-founders or connections in general; if you want to pitch your start-up/product/service/skills/etc. or if you just want to come and watch an entertaining, rapid-fire event, then this is THE one to attend.</p> <p>** The event is hosted by the 8200 EISP accelerator. The event is being sponsored by Maverick Ventures.</p> <p>So, who should register? Only if you are -</p> <p>- An entrepreneur with an idea that is looking for a co-founder; fill in your start-up and description here: <a href=\"https://goo.gl/forms/Q40YCnkWcmM5Tiii1\"><a href=\"https://goo.gl/forms/Q40YCnkWcmM5Tiii1\" class=\"linkified\">https://goo.gl/forms/Q40YCnkWcmM5Tiii1</a></a></p> <p>- Entrepreneurs at heart that are looking for a start-up to join.</p> <p>AGENDA:</p> <p>19:00-pm \u2013 Get together, snacks and drinks</p> <p>19:30-pm \u2013 Few words from the organizers</p> <p>19:40-pm - STRICTLY '60 second' speed pitching*</p> <p>20:45-pm - 'Dating' and socializing</p> <p><br/>*Suggested breakdown for 60-second pitches (Remember: practice makes perfect!):\xA0</p> <p>20s - Who you are.\xA0</p> <p>20s - What you do/are working on.\xA0</p> <p>20s - Who/what you are looking for.</p> <p>This event is free!</p> <p>\n\n\n**Event Sponsor: Maverick Ventures</p> <p>Maverick Ventures is a boutique venture capital fund based in Tel Aviv focusing on growth stage Israel-related technology ventures. The fund has made 12 investments in the last year and a half, investing on average between $1-3 Million dollars in companies with existing proven traction. The fund is run by former entrepreneurs with a track record and is backed solely by hand-picked private strategic investors from around the world, whom are proactively helpful in growth through their own networks. For more information please visit &lt;a&gt;www.maverick.vc.&lt;/a&gt;</p> <p>**8200 EISP</p> <p>8200 EISP was the first start-up accelerator in Israel, founded in late 2011. Our vision is to harness the vast network of 8200 alumni in order to promote early stage, first-time entrepreneurs from all industries. This is our way as alumni of 8200 to give back to the Israeli society.</p> <p><br/>80% of the program alumni are currently leading their start-up as founders. Our alumni raised seed funding and beyond of over $400M in total and employ more than 450 people in Israel and overseas.</p> <p><br/><a href=\"http://www.eisp.org.il/en/home\"><a href=\"http://www.eisp.org.il/en/home\" class=\"linkified\">http://www.eisp.org.il/en/home</a></a> is funded and supported by our partners, all leaders in their respective fields. Our partners include Bank Hapoalim (the largest bank in Israel), EY Israel, law firms Naschitz Brandes Amir and Pearl Cohen Zedek Latzer Baratz, the BRM Group, Star Farm Ventures, Battery Ventures, AOL Israel, Nextage, Matrix (8300), Elron and WeWork Israel.</p> <p><br/>Follow us:</p> <p>Facebook - \u05EA\u05D5\u05DB\u05E0\u05D9\u05EA \u05D4\u05D9\u05D6\u05DE\u05D5\u05EA \u05E9\u05DC \u05D9\u05D5\u05E6\u05D0\u05D9 8200 - EISP</p> <p>Meetup - <a href=\"http://www.meetup.com/8200-EISP/\"><a href=\"http://www.meetup.com/8200-EISP/\" class=\"linkified\">http://www.meetup.com/8200-EISP/</a></a></p> ",
